import $ from 'jquery';

const serverDomain = 'https://pelisplus.icu';

async function getMovies(type, keyword, page) {
  if(type == 'peliculas') {
    type = 'movies';
  } else if(type == 'temporadas') {
    type = 'new-season';
  }
  let dataResponse = {};
  if(!keyword) {
    let response = await fetch(`${serverDomain}/${type}?page=${page}`)
    let res = await response.text();
    let body = $(res);
    let videosWatch = [];
    body.find('.video-block').each(function() {
      videosWatch.push({
        image: $(this).find('.picture').find('img').attr('src'),
        title: $(this).find('.name').text().trim(),
        date: $(this).find('.date').text().trim(),
        targetWatch: $(this).find('a').attr('href')
      });
    });
    let vecPages = [];
    body.find('[data-page]').each(function() {
      if($(this).text() != '>' && $(this).text() != '<') {
        vecPages.push($(this).text());
      }
    });
    let numberPages = Math.max(...vecPages);
    dataResponse = {
      data: videosWatch,
      page: page || 1,
      numberPages
    }
  } else {
    type = 'search.html';
    let response = await fetch(`${serverDomain}/${type}?keyword=${keyword}&page=${page}`)
    let res = await response.text();
    let body = $(res);
    let videosWatch = [];
    body.find('.video-block').each(function() {
      videosWatch.push({
        image: $(this).find('.picture').find('img').attr('src'),
        title: $(this).find('.name').text().trim(),
        date: $(this).find('.date').text().trim(),
        targetWatch: $(this).find('a').attr('href')
      });
    });
    let vecPages = [];
    body.find('[data-page]').each(function() {
      if($(this).text() != '>' && $(this).text() != '<') {
        vecPages.push($(this).text());
      }
    });
    let numberPages = Math.max(...vecPages);
    dataResponse = {
      data: videosWatch,
      page: page || 1,
      numberPages
    }
  }
  return dataResponse;
}

function capitalizarPrimeraLetra(str) {
  return str.charAt(0).toUpperCase() + str.slice(1);
}

async function watchVideo(titleSearch) {
  let response = await fetch(`${serverDomain}/ver/${titleSearch}`);
  let res = await response.text();
  let body = $(res);
  if(!body.find('.video-block')[0]) {
    return {
      error: true,
      message: 'Page not found'
    }
  }
  let videoInfo = {};
  videoInfo.title = body.find('#main_bg > div:nth-child(5) > div > div.video-info-left > div.video-details > span').text().trim();
  videoInfo.titleEpisode = body.find('#main_bg > div:nth-child(5) > div > div.video-info-left > h1').text().trim();
  videoInfo.optionsStream = [];
  videoInfo.description = body.find('#rmjs-1').text().trim();
  if(videoInfo.titleEpisode.toLowerCase().includes('temporada')) {
    videoInfo.episode = parseInt(videoInfo.titleEpisode.split('Capítulo ')[1].trim());
    videoInfo.season = parseInt(videoInfo.titleEpisode.split('Temporada ')[1].trim().split(' ')[0]);
  }
  body.find('[role="presentation"]').each(function() {
    let namLanguage = capitalizarPrimeraLetra($(this).parent().attr('id').split('_')[1]);
    if(namLanguage == 'Castell') {
      namLanguage = 'Castellano';
    }
    videoInfo.optionsStream.push({
      language: namLanguage,
      option: $(this).find('a').find("span").remove().end().text().trim(),
      stream: $(this).attr('data-video')
    });
  });
  let videosWatch = [];
  body.find('#main_bg > div:nth-child(5) > div > div.video-info-left > ul').find('.video-block').each(function() {
    if($(this).find('.name').text().trim().toLowerCase().includes('temporada')) {
      videosWatch.push({
        image: $(this).find('.picture').find('img').attr('src'),
        title: $(this).find('.name').text().trim(),
        season: parseInt($(this).find('.name').text().trim().split('Temporada')[1].trim().split(' ')[0]),
        episode: parseInt($(this).find('.type').find('span').text().trim().replace('Ep ', '')),
        date: $(this).find('.date').text().trim(),
        targetWatch: $(this).find('a').attr('href')
      });
    } else {
      videosWatch.push({
        image: $(this).find('.picture').find('img').attr('src'),
        title: $(this).find('.name').text().trim(),
        date: $(this).find('.date').text().trim(),
        targetWatch: $(this).find('a').attr('href')
      });
    }
  });
  let videosRelevant = [];
  body.find('#main_bg > div:nth-child(5) > div > div.video-info-right > ul').find('.video-block').each(function() {
    videosRelevant.push({
      image: $(this).find('.picture').find('img').attr('src'),
      title: $(this).find('.name').text().trim(),
      date: $(this).find('.date').text().trim(),
      targetWatch: $(this).find('a').attr('href')
    });
  });
  return {
    videoInfo,
    videosWatch,
    videosRelevant
  }
}

